<template>
  <div id="base-container">
        <img ref="resultImage" id="result-img-bg" class="" :src="selectedResult" alt="">
        <div id="result-text-container">
            <p v-html="detailTexts[this.selectedRingId]" v-bind:style="{ width: contentWidth * 0.7 + 'px' }"></p>
        </div>
        <button id="next-btn" class="red-btn-style" @click="onConfirmAction">{{ confirmText }}</button>
  </div>
</template>

<script>
export default {
    name: 'result-view',
    props: {
        resultNumber: Number
    },
    mounted: function() {
        var resultId = this.$route.params.result_id;
        this.selectedResult = this.resultImgs[resultId];
        this.photoDataUri = this.$route.params.photoDataUri;
        let self = this;
        let resizeOb = new ResizeObserver(function(entries) {
            let rect = entries[0].contentRect;
            let width = rect.width;
            self.contentWidth = width;
        });
        resizeOb.observe(this.$refs.resultImage);
    },
    data: function() {
        return {
            resultImgs: [resultImg_diamond, resultImg_ruby, resultImg_amethyst],
            answerTextImgs: [answerTextImg_1, answerTextImg_2, answerTextImg_3, answerTextImg_4],
            selectedRingId: this.$route.params.result_id,
            selectedSloganId: this.$route.params.slogan_id,
            selectedResult: '',
            detailTexts: detailTexts,
            photoDataUri: '',
            contentWidth: 50
        }
    },
    computed: {
        confirmText: function() {
            if (this.photoDataUri == null || this.photoDataUri == '') {
                return '馬上試玩';
            } else {
                return '下一步';
            }
        }
    },
    methods: {
        onConfirmAction: function() {
            if (this.photoDataUri == null || this.photoDataUri == '') {
                this.$router.push({ name: 'Home' });
            } else {
                this.$router.push({ name: 'Share', params: { selectedRingId: this.selectedRingId, photoDataUri: this.photoDataUri, selectedSloganId: this.selectedSloganId } });
            }
        }
    }
}

const resultImg_diamond = require('@/assets/img_result_diamond.jpg');
const resultImg_ruby = require('@/assets/img_result_ruby.jpg');
const resultImg_amethyst = require('@/assets/img_result_amethyst.jpg');

const answerTextImg_1 = require('@/assets/img_answer_text_01.png');
const answerTextImg_2 = require('@/assets/img_answer_text_02.png');
const answerTextImg_3 = require('@/assets/img_answer_text_03.png');
const answerTextImg_4 = require('@/assets/img_answer_text_04.png');

const detailTexts = [
    "非常喜歡數字，<br>有超強的記憶力和演算能力，<br>喜歡秩序和整齊到甚至有點強迫症的程度。<br>不善人際關係，為人耿直，<br>是虔敬與誠實的象徵。<br>生活充滿井然有序的規律性，<br>直到他的公式化世界出現變數——<br>遇見熱情美麗的空服員麗莎，<br>有如上帝把天灰撥開一道曙光……。]<br>鑽石代表著慈愛、誠實、德望，深藍、清澈是貞節之愛的保證。",
    "善良熱情美麗大方的麗莎<br>從小在爸爸肩頭上造飛機，<br>夢想飛上天空。<br>如願進入航空業成為空服員，<br>看到乘客臉上的笑容就是她的快樂來源，<br>享受飛往世界各地冒險闖蕩。<br>不按牌理出牌的麗莎，<br>只有對浪漫求婚的憧憬無法妥協，<br>卻愛上了一板一眼的奕之……。<br>紅酒般璀璨奪目的紅寶石，<br>也被稱為「愛情之石」，<br>象徵能為人帶來幸福。",
    "迷人有魅力的Rossy<br>，身為知名兩性作家，<br>時常在廣播節目上暢談婚姻<br>、愛情相處之道。<br>與餐廳老闆思敬擁有一段令人欣羨的<br>幸福婚姻生活。<br>她的聲音彷彿是這座城市的心靈雞湯，<br>卻對自己與丈夫日益惡化的關係無能為力，<br>愛情即將在日常瑣事中逐漸被消磨殆盡了嗎？<br>紫水晶是社交之石，<br>也代表著「愛的守護石」<br>，主宰直覺與潛意識，<br>有助於開發智慧與潛能。"
];
</script>
    
<style scoped>
#base-container {
    position: fixed;
    background-color: #a8d0d9;
    width: 100%;
    height: 100%;
}
#result-text-container {
    text-align: center;
    position: fixed;
    width: 100%;
    height: 29%;
    bottom: 0%;
    /* transform: translate(-50%, -50%); */
}
#result-text-container p {
    overflow: scroll;
    position: relative;
    width: 45%;
    height: 50%;
    margin: auto;
    font-size: 2vh;
}
#result-img-bg {
    max-width: 100%;
    height: 100%;
    margin: auto;
}
#next-btn {
    font-size: 3.5vh;
    position: fixed;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 5%;
}
