<template>
  <div id="base-container">
    <img ref="backgroundImage" id="background-image" class="" :src="backgroundImage" alt="">
    <div id="photo-container">
      <img id="result-photo" :src="photoDataUri" v-bind:style="{ width: contentWidth + 'px' }" alt="">
    </div>
    <div id="buttons-group">
      <button class="red-btn-style" @click="onPlayAgain" style="margin-right: 10px;">再玩一次</button>
      &nbsp;
      <button class="red-btn-style" @click="onShareResult" style="margin-left: 10px; margin-bottom: 10px">分享結果</button>
      <br>
      <button class="red-btn-style" @click="onSavePhoto" style="margin-right: 10px;">儲存照片</button>
      &nbsp;
      <button class="red-btn-style" @click="onGoPreview" style="margin-left: 10px;">觀看預告</button>
    </div>
  </div>
</template>

<script>
export default {
  data: function() {
      return {
          resultImgs: [resultImg_diamond, resultImg_ruby, resultImg_amethyst],
          resultId: this.$route.params.selectedRingId,
          photoDataUri: this.$route.params.photoDataUri,
          photoImage: Image,
          contentWidth: 100
      }
  },
  computed: {
    backgroundImage: function() {
      return this.resultImgs[this.resultId];
    }
  },
  mounted: function() {
    if (this.photoDataUri == null || this.photoDataUri == '') {
      this.$router.push({name: 'Home'});
    }

    let img = new Image();
    img.src = this.photoDataUri;
    this.photoImage = img;

    let self = this;
    let resizeOb = new ResizeObserver(function(entries) {
        let rect = entries[0].contentRect;
        let width = rect.width;
        self.contentWidth = width;
    });
    resizeOb.observe(this.$refs.backgroundImage);
  },
  methods: {
    onPlayAgain: function() {
      this.$router.push({ name: 'CameraShot' });
    },
    onShareResult: function() {
      let hostName = window.location.hostname
      let url = `https://${hostName}/AotR/%23/result-view/result_${this.resultId}/slogan_0`;
      let link = this.getShareLink(url, `${hostName}${resultImg_diamond}`, '戒指流浪記_找回戒指_找回愛');
      window.open(link);
    },
    onSavePhoto: function() {

      let dataUri = this.createSreenshotPhoto();

      let image = new Image();
      image.src = dataUri;
      image.setAttribute("style", "-webkit-user-select:none; display:block; margin:auto; padding:env(safe-area-inset-top) env(safe-area-inset-right) env(safe-area-inset-bottom) env(safe-area-inset-left);");
      var w = window.open("");
      w.document.write(image.outerHTML);

      // window.open(dataUri, '_blank');
      
      // let link = document.createElement('A');
      // link.setAttribute("download", "AR.png");
      // let uri = dataUri.replace(/^data:image\/[^;]/, 'data:application/octet-stream');
      // link.setAttribute("href", uri);
      // link.click();
    },
    onGoPreview: function() {
      let link = 'https://www.youtube.com/watch?v=xR8fDa8Oy_Q&ab_channel=HBOAsia';
      window.open(link, '_blank');
    },
    getShareLink: function(url, picture, hashtag) {
      let linkUrl = `https://www.facebook.com/sharer/sharer.php?u=${url}%2F&picture=${picture}&hashtag=%23${encodeURIComponent(hashtag)}, "_blank", "toolbar=0,status=0`;
      return linkUrl;
    },
    createSreenshotPhoto: function() {
      let canvas = document.createElement('CANVAS');
      canvas.width = 800;
      canvas.height = 1731;
      let ctx = canvas.getContext('2d');
      let backgroundImg = this.$refs.backgroundImage;
      ctx.drawImage(backgroundImg, 0, 0, 1284, 2778, 0, 0, 800, 1731);

      let photo = this.photoImage;
      ctx.drawImage(photo, 0, 10, photo.width, photo.height * 0.65, 0, 160, 800, 950);

      return canvas.toDataURL()
    }
  }
}

const resultImg_diamond = require('@/assets/img_share_result_diamond.jpg');
const resultImg_ruby = require('@/assets/img_share_result_ruby.jpg');
const resultImg_amethyst = require('@/assets/img_share_result_amtheyst.jpg');
</script>

