<template>
<div id="base-container">
    <video ref="video" id="video-view" autoplay="true" playsinline></video>
    <canvas ref="canvas" id="video-canvas"></canvas>
    <img id="img-hand" v-show="status == 1" :src="handShapeImageUrl" alt="">
    <img id="img-answer-text" v-show="false" :src="selectedSloganImg" alt="">
    <!-- <router-link to='/result-view/123'><button id="btn-confirm" class="red-btn-style">確認</button></router-link> -->

    <div id="photo-container">
        <img id="hbo-logo" src="@/assets/logo_hbo.png" alt="">
        <img id="program-logo" src="@/assets/logo.png" alt="">
    </div>

    <button id="btn-confirm" class="red-btn-style" @click="onConfirmBtn">{{ confirmText }}</button>
    <button id="btn-hand-side" @click="changeHandSide"><img :src="handImageBtnUrl" alt=""></button>
</div>
</template>

<script>
import CameraPhoto, {FACING_MODES} from 'jslib-html5-camera-photo';

export default {
    name: "Camera",
    data: function() {
        return {
            status: 1, // 1: normal, 2: in random, 3. finish
            isLeftHanded: true,
            handImageBtnUrl: lhBtnImg,
            handShapeImageUrl: lhShapeImg,
            sloganImgs: [answerTextImg_1, answerTextImg_2, answerTextImg_3, answerTextImg_4],
            lhRingImgs: [diamondLHImg, rubyLHImg, amethystLHImg],
            rhRingImgs: [diamondRHImg, rubyRHImg, amethystRHImg],
            selectedRingId: 0,
            selectedSloganId: 0,
            isResultShown: false,
            countdownTime: 50,
            cameraPhoto: CameraPhoto,
            videoEl: Element,
            canvasEl: Element,
            ringEl: Element,
            context: CanvasRenderingContext2D,
            photoDataUri: ''
        }
    },
    mounted: function() {
        this.videoEl = this.$refs.video;
        this.ringEl = this.$refs.ring;
        this.canvasEl = this.$refs.canvas;
        this.canvasEl.width = this.screenMinLength;
        this.canvasEl.height = this.screenMaxLength;
        this.context = this.canvasEl.getContext('2d');

        this.videoEl.control = false;
        console.log('max length: ' + this.screenMaxLength);
        console.log('min length: ' + this.screenMinLength);
        this.cameraPhoto = new CameraPhoto(this.videoEl);
        this.cameraPhoto.startCamera(FACING_MODES.ENVIRONMENT, {width: 640, height: 480}).then(stream => {
            console.log(stream);
            console.log('video: ' + this.videoEl.videoWidth + ", " + this.videoEl.videoHeight);
            this.timerCallBack();
        }).catch(this.timerCallBack());
    },
    computed: {
        selectedRingImg: function() {
            if (this.isLeftHanded) {
                return this.lhRingImgs[this.selectedRingId]
            } else {
                return this.rhRingImgs[this.selectedRingId]
            }
        },
        selectedSloganImg: function() {
            return this.sloganImgs[this.selectedSloganId]
        },
        confirmText: function() {
            if (this.status == 1) {
                return "開始";
            } else if (this.status == 2) {
                return "選取中";
            } else if (this.status == 3) {
                return "拍照";
            } else {
                return "N/A";
            }
        },
        isPortrait: function() {
            return window.orientation == 0;
        },
        cameraSettings: function() {
            return this.cameraPhoto.getCameraSettings()
        },
        ringImage: function() {
            let img = new Image();
            img.src = this.selectedRingImg;
            return img;
        },
        sloganImage: function() {
            let img = new Image();
            img.src = this.selectedSloganImg;
            return img;
        }
    },
    methods: {
        getScreenWidth: function() {
            return window.innerWidth;
        },
        getScreenHeight: function() {
            return window.innerHeight;
        },
        changeHandSide: function() {
            this.isLeftHanded = !this.isLeftHanded;
            this.handImageBtnUrl = this.isLeftHanded ? lhBtnImg : rhBtnImg;
            this.handShapeImageUrl = this.isLeftHanded ? lhShapeImg : rhShapeImg;
            let self = this;
            this.ringImage.onload = function() {
                self.computeFrame();
            };
        },
        getRandomRingId: function() {
            var random = 0;
            do {
                random = Math.floor(Math.random() * 10)
            } while (random >= this.rhRingImgs.length || random == this.selectedRingId);
            return random
        },
        getRandomSloganId: function() {
            var random = 0;
            do {
                random = Math.floor(Math.random() * 10)
            } while (random >= this.sloganImgs.length || random == this.selectedSloganId);
            return random
        },
        onConfirmBtn: function() {
            if (this.status == 1 ){
                this.startCountdown();
                this.status = 2;
            } else if (this.status == 3) {
                this.getCameraPhoto();
                // this.cameraPhoto.stopCamera();
                if (this.photoDataUri != '') {
                    this.$router.push({ name: 'Result', params: {result_id: this.selectedRingId, photoDataUri: this.photoDataUri} });
                } else {
                    this.$router.push({ path: `/result-view/result_${this.selectedRingId}/slogan_${this.selectedSloganId}`});
                }
            }
        },
        startCountdown: function() {
            var timer = setInterval(() => {
                // console.log('Count time: ' + this.countdownTime);

                if (this.countdownTime <= 0) {
                    clearInterval(timer);
                    this.status = 3;
                    this.countdownTime = 100;
                    this.computeFrame();
                } else if (this.countdownTime < 30) {
                    this.countdownTime -= 1;
                    if (this.countdownTime % 2 == 0) {
                        this.selectedRingId = this.getRandomRingId();
                        this.selectedSloganId = this.getRandomSloganId();
                        this.computeFrame();
                    }
                } else if (this.countdownTime < 20) {
                    this.countdownTime -= 1;
                    if (this.countdownTime % 3 == 0) {
                        this.selectedRingId = this.getRandomRingId();
                        this.selectedSloganId = this.getRandomSloganId();
                        this.computeFrame();
                    }
                } else if (this.countdownTime < 10) {
                    this.countdownTime -= 1;
                    if (this.countdownTime % 4 == 0) {
                        this.selectedRingId = this.getRandomRingId();
                        this.selectedSloganId = this.getRandomSloganId();
                        this.computeFrame();
                    }
                } else {
                    this.selectedRingId = this.getRandomRingId();
                    this.selectedSloganId = this.getRandomSloganId();
                    this.computeFrame();
                    this.countdownTime -= 1;
                }
            }, 100);
        },
        getHtmlImage: function () {
            // let el = document.querySelector('');
        },
        getCameraPhoto: function() {
            // const config = {};
 
            // let dataUri = this.cameraPhoto.getDataUri(config);
            let dataUri = this.canvasEl.toDataURL();

            this.photoDataUri = dataUri;

            this.status = 3;
        },
        imageToDataUri: function(image, width, height) {
            // create an off-screen canvas
            var canvas = document.createElement('canvas'),
            ctx = canvas.getContext('2d');

            // set its dimension to target size
            canvas.width = width;
            canvas.height = height;

            // draw source image into the off-screen canvas:
            ctx.drawImage(image, 0, 0, width, height);

            // encode image to data-uri with base64 version of compressed image
            return canvas.toDataURL();
        },
        timerCallBack: function() {
            // console.log("CALL!!");
            // if (this.videoEl.paused || this.videoEl.ended) {
            //     return
            // }
            this.computeFrame();
            let self = this;
            setTimeout(function() {
                self.timerCallBack();
            }, 1000 / 30);
        },
        computeFrame: function() {
            this.canvasEl.width = this.getScreenWidth();
            this.canvasEl.height = this.getScreenHeight();
            // this.context.drawImage(this.videoEl, 0, 0, this.getScreenWidth(), this.getScreenHeight(), 0, 0, this.getScreenWidth(), this.getScreenHeight());
            // console.log('video: ' + this.videoEl.videoWidth + "," + this.videoEl.videoHeight);
            // console.log('screen: ' + this.getScreenWidth() + "," + this.getScreenHeight());
            // video : 480, 640
            // screen : 390. 664
            
            let screenFactor = 480 / 640
            let oriScreenWidth = this.getScreenWidth();
            let oriScreenHeight = this.getScreenHeight();
            let mScreenWidth = oriScreenHeight * screenFactor
            // console.log('canvas: ' + this.canvasEl.width + ", " + this.canvasEl.height);
            // this.context.drawImage(this.videoEl, 0, 0, 350, this.videoEl.videoHeight, 0, 0, this.getScreenWidth(), this.getScreenHeight());
            this.context.drawImage(this.videoEl, 0, 0, 480, 640, 0, 0, mScreenWidth, this.getScreenHeight());

            if (this.status != 1) {
                let scaledWidth = oriScreenWidth * 0.19;
                let factor = scaledWidth / this.ringImage.width;
                let offsetFactor = 0.28
                let xOffset = this.isLeftHanded ? (oriScreenWidth * offsetFactor) : ((oriScreenWidth * (1 - offsetFactor)) - scaledWidth);
                let yOffset = (oriScreenHeight / 2) - ((oriScreenHeight / 2) * 0.4);
                this.context.drawImage(this.ringImage, 0, 0, this.ringImage.width, this.ringImage.height, xOffset, yOffset, scaledWidth, this.ringImage.height * factor);

                let scaledWidth_s = this.getScreenWidth();
                let factor_s = scaledWidth_s / this.sloganImage.width;
                let xOffset_s = 0;
                // let yOffset_s = (this.getScreenHeight() / 2) - (this.sloganImage.height * factor_s);
                let yOffset_s = ((this.getScreenHeight() - (this.sloganImage.height * factor_s)) / 2) - 100;
                this.context.drawImage(this.sloganImage, 0, 0, this.sloganImage.width, this.sloganImage.height, xOffset_s, yOffset_s, scaledWidth_s, (this.sloganImage.height * factor_s));
            }
        }
    }
}

const lhBtnImg = require('@/assets/btn_left_hand.png');
const rhBtnImg = require('@/assets/btn_right_hand.png');
const lhShapeImg = require('@/assets/img_left_hand.png');
const rhShapeImg = require('@/assets/img_right_hand.png');
const diamondLHImg = require('@/assets/img_lh_diamond_s.png');
const diamondRHImg = require('@/assets/img_rh_diamond_s.png');
const rubyLHImg = require('@/assets/img_lh_ruby_s.png');
const rubyRHImg = require('@/assets/img_rh_ruby_s.png');
const amethystLHImg = require('@/assets/img_lh_amethyst_s.png');
const amethystRHImg = require('@/assets/img_rh_amethyst_s.png');

const answerTextImg_1 = require('@/assets/img_answer_text_01.png');
const answerTextImg_2 = require('@/assets/img_answer_text_02.png');
const answerTextImg_3 = require('@/assets/img_answer_text_03.png');
const answerTextImg_4 = require('@/assets/img_answer_text_04.png');
</script>

