<template>
  <div id="app">
    <router-view/>
    <div v-show="showBlock" id="block-view"><h1>使用手機並翻轉成直式來體驗遊戲呦～</h1></div>
  </div>
</template>
<script>
export default {
  data: function() {
    return {
      showBlock: false
    }
  },
  mounted: function() {
    var self = this;
    let ratio = window.innerWidth / window.innerHeight;
    this.showBlock = ratio > 0.7;
    window.onresize = function() {
      self.detectSizeChange();
    }
  },
  methods: {
    detectSizeChange: function() {
      let ratio = window.innerWidth / window.innerHeight;
      if (ratio > 0.7) {
        this.showBlock = true;
      } else {
        this.showBlock = false;
      }
    }
  }
}
</script>
