import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import CameraShot from '../components/CameraShot.vue'
import ResultView from '../components/ResultView.vue'
import ShareView from '../components/ShareView.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/camera-shot',
    name: 'CameraShot',
    component: CameraShot
  },
  {
    path: '/result-view/result_:result_id/slogan_:slogan_id',
    name: 'Result',
    component: ResultView
  },
  {
    path: '/share-view',
    name: 'Share',
    component: ShareView
  }
]

const router = new VueRouter({
  // mode: 'hash',
  // base: process.env.BASE_URL,
  routes,
  mode: 'hash',
  base: '/',
})

export default router
